import $ from 'jquery'


$(window).ready(function () {

    //menu
    $('.burger').click(function () {
        $(this).toggleClass('active')
        $('.nav').toggleClass('visible')
        $('header').toggleClass('opened')

        if ($('.nav').hasClass('visible')) {
            $('.nav').css('padding-top', $('header').outerHeight())
            $('.nav').css('height', '');
        }
        else {
            $('.nav').css('padding-top', '0')
            $('.nav').css('height', '0')
        }

        if ($('.burger').hasClass('active')) {
            $('.burger.active').css('height', '23px')
            $('.burger.active').css('width', '23px')
        }
        else {
            $('.burger').css('height', '')
            $('.burger').css('width', '')   
        }

    })

    if (document.getElementById('home')) {
        $(`.nav__item:nth-child(1)`).css('color', '#10c9c3')
    }
    else if (document.getElementById('about')) {
        $(`.nav__item:nth-child(2)`).css('color', '#10c9c3')
        console.log(2)
    }
    else if (document.getElementById('contact')) {
        $(`.nav__item:nth-child(3)`).css('color', '#10c9c3')
    }

    $(window).resize(function () {
        if ($(window).width() >= 768) {
            $('.nav').removeClass('visible')
            $('.burger').removeClass('active')
            $('.nav').css('padding-top', '')
        }
    })
})

export function click(i) {
    $('.nav').removeClass('visible')
    $('.burger').removeClass('active')
    $('.burger').css('height', '')
    $('.burger').css('width', '')

    $('.nav__item').css('color', '#191919')
    $(`.nav__item:nth-child(${i})`).css('color', '#10c9c3')
}