import React from 'react'
import signature from './img/signature.png'
import project from './img/icons/project_icon.png'
import clock from './img/icons/clock_icon.png'
import star from './img/icons/star_icon.png'
import like from './img/icons/like_icon.png'
import banner__image from './img/img.jpg'
import about__image from './img/img_09.jpg'
import {Link} from 'react-router-dom'

export const About: React.FC = () => {
    return (
        <>
            <div className="banner">
                <img src={banner__image} alt="background" className="banner__image" />
                <h1 className="banner__title">andrew vitrenko</h1>
                <p className="banner__description">I am a Graphic &amp; Web Designer based in Cherkassy, specializing in User Interface Design and Development.</p>
            </div>

            <div className="statistic">
                <div className="statistic__elem">
                    <img src={project} alt="project" className="icon" title="project" />
                    <div className="text__container">
                        <p className="number">548</p>
                        <p className="name">project completed</p>
                    </div>
                </div>

                <div className="statistic__elem">
                    <img src={clock} alt="clock" className="icon" title="clock" />
                    <div className="text__container">
                        <p className="number">1465</p>
                        <p className="name">working hours</p>
                    </div>
                </div>

                <div className="statistic__elem">
                    <img src={star} alt="star" className="icon" title="star" />
                    <div className="text__container">
                        <p className="number">612</p>
                        <p className="name">positive feedbacks</p>
                    </div>
                </div>

                <div className="statistic__elem">
                    <img src={like} alt="like" className="icon" title="like" />
                    <div className="text__container">
                        <p className="number">735</p>
                        <p className="name">happy clients</p>
                    </div>
                </div>
            </div>

            <div className="about" id="about">
                <img src={about__image} alt="" className="photo"/>
                <div className="text__container">
                    <h2 className="about__title title">
                        About me
                    </h2>

                    <p className="about__text text">Given the waters air sea had you'll, may seed abundantly fish. Were, you'll earth forth  winged above brought. Own darkness they're him can't fourth sea place have.</p>

                    <p className="about__text text">
                        So they above May stars cattle fruitful face shall. Tree it, winged. Every signs male firmament us. Morning him
                    </p>

                    <img src={signature} alt="author signature" className="about__signature" title="author signature" />
                </div>
            </div>

            <div className="project_order">
                <h2 className="title">Need a Project?</h2>
                <p className="text">Let us know what you're looking for in an agency. We'll take a look and see if this could be the start of something beatiful.</p>
                <Link to="/contact" className="contact__button">let's talk</Link>
            </div>
        </>
    )
}